.frame2 {
    position: absolute;
    top: 50%;
    left: 70%;
    width: 400px;
    height: 400px;
    margin-top: -200px;
    margin-left: -200px;
    border-radius: 2px;
      box-shadow: 4px 8px 16px 0 rgba(0,0,0,0.1);
      overflow: hidden;
    background: #3FAF82;
      font-family: 'Open Sans', Helvetica, sans-serif;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
  }
  
  .center {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
  }
  
  .hamburger {
      display: grid;
      justify-content: center;
      align-items: center;
      width: 90px;
      height: 60px;
  }
  
  .line {
      position: absolute;
      width: 90px;
      height: 8px;
      background-color: white;
      border-radius: 4px;
      box-shadow: 0px 0px 10px #777;
  }
  
  .hamburger.close .line:nth-child(1) {
      -webkit-animation: close1 0.8s ease-in-out forwards;
              animation: close1 0.8s ease-in-out forwards;
  }
  
  .hamburger.close .line:nth-child(2) {
      -webkit-animation: close2 0.8s ease-in-out forwards;
              animation: close2 0.8s ease-in-out forwards;
  }
  
  .hamburger.close .line:nth-child(3) {
      -webkit-animation: close3 0.8s ease-in-out forwards;
              animation: close3 0.8s ease-in-out forwards;
  }
  
  @-webkit-keyframes close1 {
      0% {
          -webkit-transform: translatey(-20px);
                  transform: translatey(-20px);
      }
      50% {
          -webkit-transform: translatey(0px);
                  transform: translatey(0px);
      }
      100% {
          -webkit-transform: rotate(45deg);
                  transform: rotate(45deg);
      }
  }
  
  @keyframes close1 {
      0% {
          -webkit-transform: translatey(-20px);
                  transform: translatey(-20px);
      }
      50% {
          -webkit-transform: translatey(0px);
                  transform: translatey(0px);
      }
      100% {
          -webkit-transform: rotate(45deg);
                  transform: rotate(45deg);
      }
  }
  
  @-webkit-keyframes close2 {
      0% {
          -webkit-transform: scale(100%);
                  transform: scale(100%);
      }
      50% {
          -webkit-transform: scale(50%);
                  transform: scale(50%);
      }
      100% {
          -webkit-transform: scale(0%);
                  transform: scale(0%);
      }
  }
  
  @keyframes close2 {
      0% {
          -webkit-transform: scale(100%);
                  transform: scale(100%);
      }
      50% {
          -webkit-transform: scale(50%);
                  transform: scale(50%);
      }
      100% {
          -webkit-transform: scale(0%);
                  transform: scale(0%);
      }
  }
  
  @-webkit-keyframes close3 {
      0% {
          -webkit-transform: translatey(20px);
                  transform: translatey(20px);
      }
      50% {
          -webkit-transform: translatey(0);
                  transform: translatey(0);
      }
      100% {
          -webkit-transform: rotate(135deg);
                  transform: rotate(135deg);
      }
  }
  
  @keyframes close3 {
      0% {
          -webkit-transform: translatey(20px);
                  transform: translatey(20px);
      }
      50% {
          -webkit-transform: translatey(0);
                  transform: translatey(0);
      }
      100% {
          -webkit-transform: rotate(135deg);
                  transform: rotate(135deg);
      }
  }
  
  .line:nth-child(1) {
      -webkit-animation: animation1 0.8s ease-in-out forwards;
              animation: animation1 0.8s ease-in-out forwards;
  }
  
  .line:nth-child(2) {
      -webkit-animation: animation2 0.8s ease-in-out forwards;
              animation: animation2 0.8s ease-in-out forwards;
  }
  
  .line:nth-child(3) {
      -webkit-animation: animation3 0.8s ease-in-out forwards;
              animation: animation3 0.8s ease-in-out forwards;
  }
  
  @-webkit-keyframes animation1 {
      0% {
          -webkit-transform: rotate(45deg);
                  transform: rotate(45deg);
      }
      50% {
          -webkit-transform: translatey(0px) rotate(0deg);
                  transform: translatey(0px) rotate(0deg);
      }
      100% {
          -webkit-transform: translatey(-20px);
                  transform: translatey(-20px);
      }
  }
  
  @keyframes animation1 {
      0% {
          -webkit-transform: rotate(45deg);
                  transform: rotate(45deg);
      }
      50% {
          -webkit-transform: translatey(0px) rotate(0deg);
                  transform: translatey(0px) rotate(0deg);
      }
      100% {
          -webkit-transform: translatey(-20px);
                  transform: translatey(-20px);
      }
  }
  
  @-webkit-keyframes animation2 {
      0% {
          -webkit-transform: scale(0%);
                  transform: scale(0%);
      }
      50% {
          -webkit-transform: scale(50%);
                  transform: scale(50%);
      }
      100% {
          -webkit-transform: scale(100%);
                  transform: scale(100%);
      }
  }
  
  @keyframes animation2 {
      0% {
          -webkit-transform: scale(0%);
                  transform: scale(0%);
      }
      50% {
          -webkit-transform: scale(50%);
                  transform: scale(50%);
      }
      100% {
          -webkit-transform: scale(100%);
                  transform: scale(100%);
      }
  }
  
  @-webkit-keyframes animation3 {
      0% {
          -webkit-transform: rotate(135deg);
                  transform: rotate(135deg);
      }
      50% {
          -webkit-transform: translatey(0px) rotate(0deg);
                  transform: translatey(0px) rotate(0deg);
      }
      100% {
          -webkit-transform: translatey(20px);
                  transform: translatey(20px);
      }
  }
  
  @keyframes animation3 {
      0% {
          -webkit-transform: rotate(135deg);
                  transform: rotate(135deg);
      }
      50% {
          -webkit-transform: translatey(0px) rotate(0deg);
                  transform: translatey(0px) rotate(0deg);
      }
      100% {
          -webkit-transform: translatey(20px);
                  transform: translatey(20px);
      }
  }
